.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    backdrop-filter: blur(35px);
    background: rgba(113, 157, 232, 0.35);
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 58.75em;
        max-width: calc(100% - 7em);
        position: relative;
        z-index: 3;
        padding: 2.5em;
        border-radius: 1.25em;
        display: inline-block;
        box-shadow: 0 .9375em 3.125em 0 rgba(0, 81, 155, 0.5);
    }
    .close-btn{
        padding: 0;
        width: 3.75em;
        height: 3.75em;
        border-radius: 50%;
        position: absolute;
        left: 100%;
        top: 0;
        margin-left: 1.25em;
        svg{
            width: .875em;
        }
    }
    &__title{
        font-size: 2.1875em;
        line-height: 123%;
        font-weight: 700;
    }
}

.modal-form{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 1.875em 2.5em;
    &__nav{
        display: flex;
        align-items: center;
        margin-top: -.625em;
    }
    &__btn{
        padding: 0 4.375em;
        margin-right: 2.5em;
    }
    .m-check{
        max-width: 21em;
    }
}



@media screen and (max-width: 992px){
    .modal{
        padding: 0;
        &:after{
            display: none;
        }
        &__dialog {
            max-width: 100%;
            width: 100%;
            height: $vh;
            overflow-y: auto;
            overflow-x: hidden;
            border-radius: 0;
            padding: 4.875em 1em 1em;
        }
        .close-btn{
            left: unset;
            right: 1em;
            top: 1.875em;
            width: 2.5em;
            height: 2.5em;
            border-radius: .375em;
            background-color: $orange;
            &:hover{
                background-color: darken($orange,10%);
            }
            svg{
                width: .875em;
            }
        }
        &__title{
            font-size: 1.125em;
        }
    }

    .modal-form{
        grid-template-columns: 100%;
        margin-top: 1.25em;
        gap: 1.25em;
        &__nav{
            flex-direction: column;
            align-items: flex-start;
            margin: 0;
        }
        &__btn{
            order: 2;
            margin: 1.25em 0 0;
            width: 100%;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }
}
