.contacts{
  padding: 1.5em 0;
}

.contact-card{
  padding: 2.5em;
  background-color: #fff;
  border-radius: 1.25em;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__icon{
    height: 5em;
    margin-bottom: 1.875em;
  }
  &__street{
    text-align: center;
    font-weight: 500;
  }
  &__phones{
    display: flex;
    flex-direction: column;
  }
  &__phone{
    font-size: 1.5em;
    line-height: 121%;
    font-weight: 700;
  }
}

.contact-time{
  display: flex;
  justify-content: space-between;
  width: calc(100% + 1.25em);
  &__title{
    font-size: .75em;
    line-height: 125%;
    font-weight: 700;
    color: $primary;
  }
  &__desc{
    font-size: 1em;
    line-height: 125%;
    font-weight: 500;
    margin-top: .3125em;
  }
}

.map{
  height: 31.25em;
  margin-top: 3.125em;
  border-radius: 1.25em;
  overflow: hidden;
}



@media screen and (max-width: 992px){
  .contacts{
    padding: .625em 0;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
      gap: .5em;
    }
  }

  .contact-card{
    padding: 1.25em;
    &__icon{
      margin-bottom: .625em;
    }
  }

  .contact-time{
    width: 100%;
    @include grid100gap(.625em);
    text-align: center;
  }

  .map{
    height: 25em;
    margin-top: 1.25em;
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .map{
    height: 18em;
  }
}
