.advantages{
  &__inner{
    padding: 0 2.5em;
  }
  &__grid{
    padding: 2.5em 0;
    background-color: #fff;
    border-radius: 1.25em;
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
  }
  .advantage-item{
    @media screen and (min-width: 993px){
      &:nth-last-child(1),
      &:nth-last-child(2),
      &:nth-last-child(3){
        padding-top: 2.5em;
        border-top: 1px solid #dae6f1;
      }
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3){
        padding-bottom: 2.5em;
        padding-top: 0;
        border-top: none;
      }
      &:nth-child(3n - 2){
        border-right: 1px solid #dae6f1;
        width: calc(100% - 2.5em);
        margin-left: auto;
      }
      &:nth-child(3n - 1){
        padding-left: 2.5em;
        padding-right: 2.5em;
      }
      &:nth-child(3n){
        border-left: 1px solid #dae6f1;
        width: calc(100% - 2.5em);
        padding-left: 2.5em;
      }
    }
  }
}

.advantage-item{
  font-weight: 700;
  &._type2{
    border-radius: 1.25em;
    background-color: #fff;
    padding: 2.5em;
  }
  &__icon{
    height: 5em;
    display: block;
    width: auto;
    margin-bottom: 1em;
  }
}

.work-with{
  padding: 1.5em 0 3.125em;
}

.trust{
  padding: 3.125em 3.75em;
  background-color: #fff;
  border-radius: 1.25em;
  &__grid{
    margin-top: 1.875em;
  }
}

.trust-item{
  @include center;
  img{
    width: 100%;
    display: block;
  }
}

.partners{
  padding-top: 3.125em;
  &__top{
    padding: 0 2.5em;
  }
  &__grid{
    margin-top: 1.875em;
  }
}

.partner-item{
  @include imgCover(52.9%);
  border-radius: 1.25em;
  background-color: #fff;
}

.new{
  padding: 1.5em 0;
  &__link{
    position: absolute;
    bottom: 2.5em;
    left: 16.25em;
    z-index: 2;
  }
}

.new-slider{
  .slider__wrap{
    border-radius: 1.25em;
  }
  .swiper{
    &-nav{
      position: absolute;
      bottom: 2.5em;
      left: 1.25em;
    }
  }
}

.new-slide{
  border-radius: 1.25em;
  background-color: $primary;
  background-image: url(../img/new-bg.svg);
  background-repeat: no-repeat;
  background-size: 45em auto;
  background-position: right 2.5em bottom 0;
  color: #fff;
  padding: 2.5em 2.5em 10em;
  height: 100%;
  min-height: 25em;
  position: relative;
  &__img{
    position: absolute;
    height: 100%;
    width: 21.25em;
    right: 15em;
    bottom: 0;
    display: flex;
    align-items: flex-end;
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__content{
    max-width: 31.25em;
  }
  &__title{
    margin-top: .42em;
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .advantages{
    &__inner{
      padding: 0;
    }
    &__top{
      padding-left: 1em;
    }
    &__grid{
      padding: 1.25em 1em;
      border-right: .625em;
      margin-top: .625em;
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: 1.25em 1em;
    }
  }

  .advantage-item{
    &._type2{
      border-radius: .625em;
      padding: 1.25em 1em;
    }
    &__icon{
      height: 3.0625em;
      margin-bottom: .625em;
    }
    br{
      display: none;
    }
    &__title{
      span{
        display: none;
      }
    }
  }

  .work-with{
    padding: .9375em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.875em;
    }
  }

  .trust{
    padding: 1.25em 1em;
    border-radius: .625em;
  }

  .partners{
    padding-top: 0;
    &__top{
      padding: 0 1em;
    }
    &__grid{
      margin-top: .625em;
    }
  }

  .new{
    padding: .9375em 0;
    &__link{
      left: 1em;
      bottom: 1.25em;
    }
  }

  .new-slider{
    .slider__wrap{
      border-radius: .625em;
    }
    .swiper{
      &-nav{
        bottom: unset;
        top: 9.375em;
        left: .5em;
      }
    }
  }

  .new-slide{
    background-image: url(../img/new-bg2.svg);
    padding: 1.25em 1em;
    min-height: 23.75em;
    border-radius: .625em;
    background-size: 16em auto;
    background-position: right 0 bottom 0;
    &__img{
      right: 0;
      width: 10.875em;
      font-size: 1.25em;
    }
    &__title{
      @include truncate(4);
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    &__grid{
      grid-template-columns: calc(53% - .25em) calc(48% - .25em);
      column-gap: .5em;
    }
  }

  .trust{
    &__grid{
      margin-top: .625em;
      grid-template-columns: repeat(3,calc(100% / 3 - 1.125em / 3));
      gap: .5625em;
    }
  }

  .partners{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5625em / 2));
      gap: .5625em;
    }
  }

  .partner-item{
    border-radius: .625em;
  }

  .new{
    &__link{
      width: calc(100% - 2em);
    }
  }

  .new-slide{
    &__img{
      font-size: 1em;
    }
    &__content{
      max-width: 10.75em;
    }
    &__title{
      font-size: .875em;
      margin-top: .71em;
    }
  }
}
