.footer{
  margin-top: 3.125em;
  padding-top: 1.875em;
  background-color: $primary;
  color: #fff;
  &__top{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    width: 21.25em;
    flex-shrink: 0;
    margin-right: 1.375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    margin-left: auto;
    flex-shrink: 0;
    height: 5.375em;
    padding: 0 3.375em;
  }
  &__middle{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: 43.75em calc(100% - 50em);
    justify-content: space-between;
    gap: 1.5em;
  }
  &__hint{
    max-width: 32.5em;
    color: $lightBlue;
  }
  &__c,
  &__conf{
    font-size: .875em;
    line-height: 121%;
  }
  &__conf{
    opacity: .7;
    text-decoration: underline;
    &:hover{
      color: #fff;
      opacity: 1;
    }
  }
  &__by{
    display: flex;
    align-items: center;
    img{
      width: 6.25em;
      margin-left: .625em;
    }
  }
}

.footer-nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: .875em;
    line-height: 121%;
    font-weight: 600;
    margin-right: 4.28em;
  }
}

.footer-contacts{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .625em));
  gap: .75em 1.25em;
  &__item{
    @include grid100gap(.3125em);
    @media screen and (min-width: 577px){
      br{
        display: none;
      }
    }
  }
  &__title{
    font-size: .75em;
    line-height: 120%;
    font-weight: 700;
  }
  &__desc{
    font-weight: 500;
    color: $lightBlue;
    a:hover{
      color: #fff;
    }
  }
}

.footer-bottom{
  font-weight: 500;
  margin-top: 1.875em;
  padding: 1.875em 0;
  background: #00498c;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}


@media screen and (max-width: 992px){
  .footer{
    margin-top: .9375em;
    padding-top: 1.25em;
    overflow: hidden;
    &__top{
      flex-wrap: wrap;
    }
    &__logo{
      width: auto;
      img{
        height: 3em;
        width: auto;
      }
    }
    &__btn{
      width: 4.5em;
      height: 2.875em;
      padding: 0;
      background-image: url(../img/mail.svg);
      @include bgImgSize(1.5em);
      span{
        display: none;
      }
    }
    &__middle{
      margin-top: 2.25em;
      grid-template-columns: 100%;
    }
  }

  .footer-nav{
    order: 2;
    width: calc(100% + 1.3125em);
    margin-top: .625em;
    flex-wrap: wrap;
    flex-shrink: 0;
    &__link{
      margin: 1.67em 1.75em 0 0;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__conf{
      margin-top: 1.2em;
    }
    &__by{
      margin-top: 1.25em;
    }
  }

  .footer-nav{
    &__link{
      font-size: .75em;
    }
  }

  .footer-contacts{
    grid-template-columns: 100%;
  }

  .footer-bottom{
    padding: 1.25em 0;
    &__inner{
      flex-direction: column;
    }
  }
}
