.about-page{
  padding: 1.5em 0;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 43.75em) 42.5em;
    gap: 1.25em;
  }
}

.we-give,
.about {
  padding: 2.5em;
  border-radius: 1.25em;
}

.about{
  background-color: #fff;
  &__logo{
    margin: 1.25em auto 0;
    width: 13.75em;
    display: block;
    max-width: 100%;
  }
  .text{
    margin-top: 1.875em;
    b{
      font-weight: 600;
    }
  }
}

.we-give{
  background: #d2e1fb;
  &__grid{
    margin-top: 5.375em;
    display: grid;
    grid-template-columns: 1fr 1.1fr 1fr;
    gap: 6.25em .625em;
  }
}

.give-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__icon{
    height: 5em;
    margin-bottom: 1.25em;
  }
  &__title{
    font-weight: 600;
    font-size: .875em;
    line-height: 121%;
  }
}

.feedback{
  padding: 1.5em 0;
  &__inner{
    padding: 2.5em 2.5em 4.5em;
    background-color: $primary;
    background-image: url(../img/feedback-bg.svg);
    background-size: cover;
    background-position: center;
    border-radius: 1.25em;
    color: #fff;
    display: grid;
    grid-template-columns: 43em 14.625em 15em;
    gap: 1.5625em;
    align-items: flex-end;
  }
  &__desc{
    margin-top: 1.25em;
    max-width: 38.5em;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    padding: 0 3.5em;
    height: 5.375em;
  }
}

.service-advantage{
  padding: 1.5em 0;
  &__inner{
    background-color: #fff;
    border-radius: 1.25em;
    padding: 2.5em;
    display: grid;
    grid-template-columns: 30em 28.75em;
    gap: 1.375em 13.75em;
  }
  &__col{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 5em;
    margin-right: 1.25em;
  }
  &__title{
    font-weight: 700;
    text-transform: uppercase;
  }
  .text{
    ul{
      column-count: 1;
    }
  }
}

.about-footer{
  padding: 1.5em 0;
  &__inner{
    display: grid;
    grid-template-columns: 30em calc(100% - 31.25em);
    gap: 1.25em;
  }
}

.work{
  background-color: #d2e1fb;
  padding: 2.5em 2.5em 45em;
  border-radius: 1.25em;
  background-image: url(../img/work-bg.svg);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 100% auto;
  &__desc{
    margin-top: 2.25em;
  }
}

.requisites{
  padding: 2.5em;
  background-color: #fff;
  border-radius: 1.25em;
  &__items{
    margin-top: 2.25em;
  }
  &__item{
    display: grid;
    grid-template-columns: 18.625em calc(100% - 19.625em);
    gap: 1em;
    align-items: center;
    padding: 1em 2.5em;
    min-height: 5.625em;
    border-radius: 1.25em;
    font-weight: 500;
    &:nth-child(odd){
      background: #d2e1fb;
    }
    &-title,
    &-desc{
      font-size: 1.125em;
      line-height: 167%;
    }
    &-title{
      font-weight: 700;
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .about-page{
    padding: .625em 0;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .about{
    padding: 1.25em 1em;
    &__logo{
      margin-top: 1.25em;
      width: 9.75em;
    }
    .text{
      margin-top: 1.25em;
    }
  }

  .we-give{
    padding: 1.25em 1em;
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
      gap: 1.25em .5em;
    }
  }

  .give-card{
    &__icon{
      height: 3.0625em;
      margin-bottom: .625em;
    }
  }

  .feedback{
    padding: .625em 0;
    &__inner{
      padding: 1.25em 1em;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    &__btn{
      height: 3.75em;
    }
  }

  .service-advantage{
    padding: .625em 0;;
    &__inner{
      padding: 1.25em 1em;
      grid-template-columns: 100%;
    }
  }

  .about-footer{
    padding: .625em 0;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .work{
    padding: 1.25em 1em 10.5em;
    background-image: url(../img/work-bg2.svg);
    background-size: 15.5em auto;
    &__desc{
      margin-top: .625em;
    }
  }

  .requisites{
    padding: 1.25em 1em;
    &__items{
      margin-top: 1.25em;
    }
    &__item{
      padding: .5em;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    border-radius: .625em;
    .text{
      p{
        b{
          font-size: 1.17em;
        }
      }
    }
  }

  .we-give{
    border-radius: .625em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .25em));
    }
  }

  .give-card{
    &__title{
      font-size: .5625em;
    }
  }

  .feedback{
    &__inner{
      border-radius: .625em;
      background-image: url(../img/feedback-bg2.svg);
    }
    &__desc,
    &__desc2{
      text-align: center;
    }
    &__desc2{
      max-width: 90%;
      margin: 0 auto;
    }
    &__btn{
      width: 100%;
    }
  }

  .service-advantage{
    &__inner{
      border-radius: .625em;
    }
    &__col{
      align-items: flex-start;
    }
    &__icon{
      width: 3.0625em;
      margin-right: .5em;
    }
    &__title{
      font-size: .875em;
    }
  }

  .work{
    border-radius: .625em;
  }

  .requisites{
    border-radius: .625em;
    &__item{
      border-radius: .625em;
      grid-template-columns: 100%;
      gap: .75em;
      &-title,
      &-desc{
        font-size: .75em;
      }
    }
  }
}
