.main{
  padding: 1.5em 0;
}

.main-slider{
  overflow: hidden;
  position: relative;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-scrollbar{
      display: block;
      width: calc(50% - .625em - 16.25em);
      top: 2.875em;
      left: 13.75em;
      height: .3125em;
      position: absolute;
      background-color: rgba(#fff,.3);
      z-index: 3;
      &-drag{
        background-color: $orange;
      }
    }
    &-nav{
      position: absolute;
      z-index: 3;
      bottom: 2.5em;
      left: 1.25em;
    }
  }
}

.main-card{
  height: 100%;
  border-radius: 1.25em;
  padding: 2.5em;
  color: #fff;
  background: linear-gradient(135deg, #00519b 0%, #4184f3 100%);
  min-height: 33.75em;
  overflow: hidden;
  position: relative;
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    height: 27.5em;
    width: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    max-width: 21em;
    margin-top: .625em;
    font-weight: 700;
  }
}

.main-nav{
  padding: 2.5em;
  background-color: #fff;
  border-radius: 1.25em;
  display: flex;
  flex-direction: column;
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &._col2{
      column-count: 2;
      display: block;
      flex-grow: 1;
      max-width: 25em;
      .main-nav__link{
        width: 100%;
        display: inline-block;
      }
    }
    &._full{
      max-width: 100%;
    }
    &._small{
      max-width: 25em;
    }
  }
  &__link{
    font-size: .875em;
    line-height: 121%;
    font-weight: 600;
    margin-bottom: 1em;
  }
}

.main-contacts{
  margin-top: auto;
  padding-top: 1em;
  display: grid;
  grid-template-columns: calc(100% - 15em) 13.75em;
  gap: 1.25em;
}

.help{
  height: 5em;
  border-radius: 1.25em;
  background-color: $back;
  display: flex;
  align-items: center;
  padding: 0 1.25em;
  img{
    margin-right: 1.25em;
    width: 2.5em;
  }
  &__info{
    @include grid100gap(.25em)
  }
}

.main-slider2{
  margin-top: 1.25em;
  overflow: hidden;
  &__wrap{
    width: calc(100% / 6 - 6.25em / 6);
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 100%;
      height: auto;
      padding-right: 1.25em;
    }
  }
}

.main-card2{
  background-color: #C5D8FA;
  border-radius: 1.25em;
  padding: 2em 1.25em;
  min-height: 13.75em;
  position: relative;
  overflow: hidden;
  display: block;
  &__title{
    font-weight: 700;
  }
  &__img{
    right: 0;
    bottom: 0;
    position: absolute;
    width: 10.5em;
    height: 8.3125em;
    max-width: 100%;
    max-height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    &__grid{
      display: flex;
      flex-direction: column;
    }
  }

  .main-slider{
    .swiper{
      &-slide{
        grid-template-columns: 100%;
        gap: .625em;
      }
      &-scrollbar{
        width: calc(100% - 10.25em);
        left: unset;
        right: 1em;
        top: 1.5em;
      }
      &-nav{
        bottom: 5.625em;
        left: .75em;
      }
    }
  }

  .main-card{
    padding: 1.25em 1em;
    min-height: 27em;
    &__img{
      height: 22em;
      max-height: 100%;
    }
  }

  .main-nav{
    padding: 0;
    background-color: transparent;
    &__links{
      display: none !important;
    }
  }

  .main-contacts{
    grid-template-columns: 100%;
    margin: 0;
    padding: 0;
    &__link{
      background-color: $orange;
      &:hover{
        background-color: darken($orange,10%);
      }
    }
  }

  .help{
    display: none;
  }

  .main-slider2{
    margin: 0 0 1.25em;
    order: -1;
    &__wrap{
      width: calc(25% - .5em);
    }
    .swiper{
      &-container{
        width: calc(100% + .5em);
      }
      &-slide{
        padding-right: .5em;
      }
      &-scrollbar{
        display: block;
        width: 8.75em;
        margin: 1.25em auto 0;
        height: .25em;
        background-color: #C5D8FA;
        &-drag{
          background-color: $primary;
        }
      }
    }
  }

  .main-card2{
    min-height: 6.75em;
    padding: .625em 1em;
    &__img{
      width: 4.625em;
      height: 3em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-slider{
    .swiper{
      &-scrollbar{
        width: calc(100% - 7.25em);
        top: 1.375em;
      }
    }
  }

  .main-card{
    min-height: 13.75em;
    border-radius: .625em;
    &__img{
      height: 8.75em;
    }
  }

  .main-slider2{
    &__wrap{
      width: calc(50% - .25em);
    }
  }

  .main-card2{
    border-radius: .625em;
  }
}
