.catalog-item3{
  padding: 1.5em 0;
}

.product-info{
  padding: 2.5em 2.5em 6.25em;
  background-color: #fff;
  border-radius: 1.25em;
  &._col2{
    @media screen and (min-width: 993px){
      grid-column: span 2;
    }
  }
  .text{
    margin-top: 1.25em;
    &._gap0{
      p,li{
        margin-bottom: 0 !important;
      }
    }
    &._gap10{
      p,li{
        @include lastM(0 0 .625em);
      }
    }
  }
  &__desc{
    text-transform: uppercase;
    color: $primary;
    font-weight: 700;
    margin-top: .875em;
  }
}

._full{
  grid-column: 1/-1;
}

.door-desc{
  @media screen and (min-width: 993px){
    ._col2{
      grid-column: span 2;
    }
    &__grid._type2{
      grid-template-columns: repeat(2,calc(50% - 1.25em));
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 1.25em 2.5em;
    margin-top: 2.25em;
  }
  .text{
    margin-top: 0;
  }
}

.construction{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 1.25em 2.5em;
    margin-top: 2.5em;
    &._mt0{
      margin-top: 0;
    }
  }
  &__img{
    width: 100%;
    display: block;
    position: sticky;
    top: 1em;
    border-radius: 1.25em;
    &._small{
      max-width: 31.25em;
      margin: 0 auto;
    }
    &._small2{
      max-width: 30em;
    }
  }
  .text{
    margin-top: 0;
  }
}

.product-desc{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 1.25em 2.5em;
  }
}

.mounting{
  &__top{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 5em;
    margin-right: 1.25em;
  }
  &__title{
    font-size: 1.125em;
    line-height: 130%;
    font-weight: 700;
  }
  .text{
    max-width: 81.25em;
    margin-top: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .catalog-item3{
    padding: .625em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: .625em;
    }
  }

  .product-info{
    padding: 1.25em 1em;
    .text{
      margin-top: .625em;
    }
  }

  .door-desc{
    &__grid{
      grid-template-columns: 100%;
      margin-top: .625em;
    }
    &:not(._open){
      .text:nth-child(1){
        p:nth-child(2){
          @include truncate(4);
          margin-bottom: 0;
          &~*{
            display: none;
          }
        }
        &~.text{
          display: none;
        }
      }
    }
  }

  .construction{
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
  }

  .construction1{
    &:not(._open){
      .text{
        li:nth-child(1){
          @include truncate(10);
          margin-bottom: 0;
          &~*{
            display: none;
          }
        }
      }
    }
  }

  .construction2{
    &:not(._open){
      .text{
        p:nth-child(2){
          @include truncate(5);
          margin-bottom: 0;
          &~*{
            display: none;
          }
        }
        ol{
          @include truncate(5);
          margin-bottom: 0;
          li{
            display: inline;
            padding-left: 0;
            &:before{
              display: none;
            }
          }
        }
        ol~*{
          display: none;
        }
      }
    }
  }

  .product-desc{
    &__grid{
      grid-template-columns: 100%;
    }
    &:not(._open){
      .product-desc__col:nth-child(1){
        p:nth-child(2){
          @include truncate(4);
          margin-bottom: 0;
          &~*{
            display: none;
          }
        }
        &~*{
          display: none;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .product-info{
    border-radius: .625em;
  }

  .mounting{
    &__icon{
      width: 3.0625em;
      margin-right: .5em;
    }
    &__title{
      font-size: .875em;
    }
  }
}
