.header{
  position: relative;
  padding-top: 6.25em;
  &__inner{
    display: flex;
    padding: 1.25em 0;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 19.625em;
    margin-right: 2.875em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.burger{
  display: none;
}

.nav,
.menu,
.menu__scroll{
  display: flex;
  align-items: center;
  flex-grow: 1;
}

.menu{
  &__btn{
    height: 5.375em;
    padding: 0 3.375em;
    margin-left: .625em;
  }
}

.nav{
  padding: 0 3.875em;
  background-color: #fff;
  border-radius: 1.25em;
  justify-content: space-between;
  height: 5.375em;
  &__link{
    font-weight: 600;
    font-size: .875em;
    line-height: 121%;
    &._active{
      color: $primary;
    }
  }
}

.header-contacts{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  background-color: #fff;
  &__inner{
    align-items: center;
    height: 6.25em;
  }
  &__item{
    @include grid100gap(.3125em);
    @media screen and (min-width: 577px){
      br{
        display: none;
      }
    }
  }
  &__title{
    font-size: .75em;
    line-height: 120%;
    font-weight: 700;
    color: $primary;
  }
  &__desc{
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 2em 0 .625em;
    background-color: #fff;
    z-index: 10;
    position: sticky;
    box-shadow: 0 .25em .625em rgba(0, 0, 0, 0.1);
    &__inner{
      padding: 0;
      justify-content: space-between;
    }
    &__logo{
      width: 8.75em;
      position: relative;
      z-index: 11;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    padding: 5em 0 0;
    z-index: 10;
    &__scroll{
      padding: .625em 1em 1.25em;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: unset;
      overflow-y: auto;
      height: 100%;
    }
    &__bottom{
      padding-top: 2em;
      margin-top: auto;
      width: 100%;
      position: relative;
    }
    &__btn{
      margin: 2em 0 0;
      width: calc(50% - .625em);
      height: 3.75em;
      padding: 0;
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }

  .nav{
    padding: 0;
    background-color: transparent;
    flex-direction: column;
    align-items: flex-start;
    justify-content: unset;
    flex-grow: unset;
    height: auto;
    &__link{
      @include lastM(0 0 .86em);
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 2.5em;
    height: 2.5em;
    display: flex;
    border-radius: .375em;
    padding: 0;
    img{
      @include centerAbsolute;
      width: 1.0625em;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 1.625em;
    }
  }

  .header-contacts{
    position: static;
    background-color: transparent;
    .container{
      padding: 0;
    }
    &__inner{
      height: unset;
      grid-template-columns: repeat(2,calc(50% - .625em));
      row-gap: 1.4375em;
    }
    &__item{
      &--address{
        grid-column: 1/-1;
      }
    }
  }

}


