.services{
  padding: 1.5em 0;
  &__inner{
    border-radius: 1.25em;
    background-color: $primary;
    padding: 2.5em;
    color: #fff;
    overflow: hidden;
  }
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 2.5em;
  }
}

.service-card{
  padding: 2.1875em 2.8125em 2.1875em 2.5em;
  box-shadow: 0 .625em 1.5625em 0 rgba(0, 81, 155, 0.25);
  background: #d2e1fb;
  border-radius: 1.25em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: $primary;
  font-weight: 700;
  position: relative;
  &._active{
    background-color: $back;
    @media screen and (min-width: 993px){
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      box-shadow: none;
      .service-card__rect{
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        height: 2.5em;
        background-color: $back;
        display: block;
      }
      .service-card__round{
        display: block;
        position: absolute;
        width: 2.5em;
        height: 2.5em;
        background-color: $back;
        top: 100%;
        &:before{
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: $primary;
        }
      }
      .service-card__round1{
        right: 100%;
        &:before{
          border-bottom-right-radius: 1.25em;
        }
      }
      .service-card__round2{
        left: 100%;
        &:before{
          border-bottom-left-radius: 1.25em;
        }
      }
    }
  }
  &:hover{
    box-shadow: none;
  }
  &__round,
  &__rect{
    display: none;
  }
  &__icon{
    width: 6.25em;
    flex-shrink: 0;
    margin-left: 1em;
    filter: drop-shadow(3.75em 3.75em 6.25em rgba(16, 45, 97, 0.08));
  }
}

.m-section{
  padding: 3.125em 0;
}

.collection{
  &__top{
    padding-left: 2.5em;
  }
}

.swiper-button{
  transition: all .3s;
  width: 5em;
  height: 5em;
  @include center;
  border: .1875em solid $primary;
  border-radius: 1.25em;
  &._white{
    border-color: #fff;
    path{
      stroke: #fff;
    }
  }
  &:hover{
    background-color: $primary;
    background-color: $primary;
    path{
      stroke: #fff;
    }
  }
  svg{
    width: .6875em;
  }
}

.swiper-nav{
  display: flex;
  .swiper-button{
    position: static;
    margin: 0 0 0 1.25em;
  }
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      padding-right: 1.25em;
    }
    &-scrollbar{
      display: none;
      height: 4px;
      background-color: #C5D8FA;
      margin: 1.25em auto 0;
      border-radius: 6.25em;
      width: 8.75em;
      &-drag{
        border-radius: 6.25em;
        background-color: $primary;
      }
    }
  }
}

.collection-slider{
  margin-top: 3.125em;
  .swiper{
    &-slide{
      width: 25%;
    }
    &-nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 1.0625em;
    }
  }
}

.product-card{
  height: 100%;
  padding: 2.5em;
  background-color: #fff;
  border-radius: 1.25em;
  display: flex;
  flex-direction: column;
  &__img{
    @include imgCover(100%);
    border-radius: 1.25em;
    margin-bottom: 2.1875em;
  }
  &__title{
    font-weight: 700;
    text-align: center;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.875em;
  }
  &__btn{
    width: 100%;
  }
}


@media screen and (max-width: 992px){
  .services{
    padding: .9375em 0;
    .container{
      padding: 0;
    }
    &__inner{
      border-radius: .625em;
      padding: 1.25em 1em;
    }
    &__top{
      padding-left: 1em;
    }
    &__grid{
      margin-top: .625em;
      grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
      gap: .5em;
    }
  }

  .services2{
    .service-card:not(._active){
      color: #fff;
      background-color: transparent;
    }
  }

  .service-card{
    padding: 1.25em;
    border-radius: .625em;
    &__icon{
      width: 2.625em;
    }
  }

  .m-section{
    padding: .9375em 0;
  }

  .collection{
    overflow: hidden;
    &__top{
      padding-left: 1em;
    }
  }

  .collection-slider{
    margin-top: 1.375em;
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-container{
        width: calc(100% + .5em);
      }
      &-nav{
        display: none;
      }
      &-slide{
        width: 16.875em;
        padding-right: .5em;
      }
      &-scrollbar{
        display: block;
      }
    }
  }

  .product-card{
    border-radius: .9375em;
    padding: 1.875em 1.5em 1em;
    &__top{
      padding: 0 .5em;
    }
    &__img{
      border-radius: .9375em;
      margin-bottom: .5em;
    }
    &__title{
      font-size: .75em;
    }
    &__nav{
      padding-top: .625em;
    }
  }

  .swiper-nav{
    .swiper-button{
      margin: 0 0 0 .5em;
    }
  }

  .swiper-button{
    width: 2.125em;
    height: 2.125em;
    border-width: .125em;
    border-radius: .625em;
    svg{
      width: .375em;
    }
  }
}


@media screen and (max-width: 576px){
  .service-card{
    padding: 1.25em .875em .875em;
    flex-direction: column;
    &__icon{
      width: auto;
      height: 2.625em;
      margin: 0 0 .625em;
      order: -1;
    }
    &__title{
      font-size: .75em;
    }
  }
}
