.delivery-variants{
  .info-card{
    padding-bottom: 2.5em;
  }
  &__ru{
    width: calc(100% + 6.25em);
    margin: 2.5em 0 0 -6.25em;
    @include center;
    img{
      width: 2.125em;
      margin-right: .75em;
    }
    &-text{
      font-size: 1.125em;
      line-height: 167%;
      font-weight: 500;
      color: $primary;
      b{
        font-weight: 700;
      }
    }
  }
}

.cooperate{
  padding: 1.5em 0;
}

.cooperate-info{
  padding: 2.5em;
  grid-column: 1/2;
  grid-row: 1/3;
  border: 3px solid #d2e1fb;
  border-radius: 1.25em;
  display: flex;
  align-items: center;
  color: $primary;
  &__text{
    font-weight: 700;
  }
}

.payment{
  padding: 1.5em 0;
  &__inner{
    padding: 2.5em;
    background: #d9e6fc;
    border-radius: 1.25em;
    display: grid;
    grid-template-columns: 16.25em calc(100% - 25em);
    gap: 1.25em;
    justify-content: space-between;
  }
  &__col{
    display: flex;
    flex-direction: column;
  }
  &__title{
    font-weight: 600;
    margin-top: 1.25em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 2em;
  }
  &__btn{
    padding: 0 3.5em;
  }
  .advantage-item{
    font-weight: 500;
    min-height: 18.75em;
  }
}

.prepay{
  padding: 1.5em 0;
  &__inner{
    padding: 2.5em;
    background-color: #fff;
    border-radius: 1.25em;
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 5em;
    margin-right: 1.25em;
  }
  &__content{
    max-width: 60em;
    @include grid100gap(.375em);
  }
}


@media screen and (max-width: 992px){
  .delivery-variants{
    .info-card{
      position: relative;
      padding-bottom: 1.25em;
      @media screen and (min-width: 577px){
        padding-bottom: 2.5em;
      }
    }
    .info-card__title{
      position: absolute;
      min-height: 3.0625em;
      display: flex;
      align-items: center;
      left: 4.625em;
      top: 1.25em;
      @media screen and (min-width: 577px){
        left: 9em;
        min-height: 5em;
        top: 2.5em;
      }
    }
    .text{
      margin-top: .25em;
    }
    &__ru{
      width: 100%;
      margin: 1.25em 0 0;
      align-items: flex-start;
      img{
        width: 2em;
        margin-right: 1em;
      }
      &-text{
        font-size: .75em;
        line-height: 135%;
        b{
          display: block;
          font-size: 1.17em;
        }
      }
    }
  }

  .cooperate{
    padding: .3125em 0 .625em;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
      gap: .5em;
    }
  }

  .cooperate-info{
    grid-row: unset;
    padding: .75em 1em;
  }

  .payment{
    padding: .625em 0;
    &__inner{
      padding: 1.25em 1em;
      grid-template-columns: 100%;
    }
    &__title{
      margin-top: .625em;
      max-width: 82%;
    }
    .advantage-item{
      min-height: 9em;
    }
  }

  .prepay{
    padding: .625em 0;
    &__inner{
      padding: 1.25em 1em;
      flex-direction: column;
      text-align: center;
    }
    &__icon{
      width: 3.625em;
      margin: 0 0 .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .cooperate{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .25em));
    }
  }

  .cooperate-info{
    border-width: 2px;
    border-radius: .625em;
    .h1{
      font-size: .75em;
    }
    .text24{
      font-size: .5625em;
    }
  }

  .payment{
    &__inner{
      border-radius: .625em;
      padding-bottom: 6.25em;
      position: relative;
    }
    &__nav{
      position: absolute;
      padding: 1.25em 1em;
      left: 0;
      bottom: 0;
      width: 100%;
    }
    &__btn{
      width: 100%;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: .625em;
    }
    .advantage-item{
      text-align: center;
      min-height: unset;
      padding-right: 1.5em;
      padding-left: 1.5em;
      &__icon{
        margin-left: auto;
        margin-right: auto;
      }
    }
  }

  .prepay{
    &__inner{
      border-radius: .625em;
    }
    &__content{
      gap: .625em;
    }
  }
}
