.variants{
  padding: 1.5em 0;
  &__inner{
    padding: 2.5em 2.5em 0;
    background: #c5d8fa;
    border-radius: 1.25em;
  }
  &__items{
    margin-top: 1.875em;
  }
  &__item{
    height: 6.25em;
    @include center;
    border-radius: 1.25em 1.25em 0 0;
    font-weight: 700;
    text-transform: uppercase;
    color: $primary;
    &._active{
      background-color: $back;
    }
    span{
      font-size: 1.5em;
    }
  }
}

.product-brand{
  padding: 2.5em;
  border-radius: 1.25em;
  background-color: #C5D8FA;
  @include center;
  img{
    mix-blend-mode: darken;
    display: block;
    max-width: 100%;
    max-height: 100%;
  }
}

.complectation{
  grid-row: span 2;
  &__img{
    width: 100%;
    display: block;
    margin-top: 2.875em;
  }
}

.drawing{
  padding-bottom: 2.5em;
  @include grid100gap(3.125em);
}

.drawing-slider{
  margin-top: 1.25em;
  @media screen and (min-width: 993px){
    .swiper{
      &-container{
        width: 100%;
      }
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
        gap: 3.125em 2.5em;
      }
      &-slide{
        padding-right: 0;
        width: 100% !important;
      }
    }
  }
}

.drawing-card{
  &__top{
    display: flex;
    align-items: center;
    &:before{
      content: '';
      display: block;
      width: 1.25em;
      height: 1.25em;
      background: #d7e4f4;
      flex-shrink: 0;
      margin-right: 1.25em;
    }
  }
  &__title{
    font-weight: 700;
    color: $primary;
  }
  &__img{
    margin-top: .625em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.m-table{
  table{
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
  }
  th{
    height: 3.5em;
    background-color: $primary;
    color: #fff;
    font-weight: 600;
    &:first-child{
      border-top-left-radius: 1.25em;
    }
    &:last-child{
      border-top-right-radius: 1.25em;
    }
  }
  th,td{
    border-right: 2px solid #3374af;
    padding-left: 1.25em;
    padding-right: 1.25em;
    &:last-child{
      border-right: none;
    }
  }
  tbody{
    tr{
      background-color: #D7E4F4;
      &:last-child{
        td{
          &:first-child{
            border-bottom-left-radius: 1.25em;
          }
          &:last-child{
            border-bottom-right-radius: 1.25em;
          }
        }
      }
    }
  }
  td{
    height: 3.75em;
  }
}

.drawing-table{
  th,td{
    text-align: center;
    &:nth-child(1),
    &:nth-child(4){
      width: 13.75em;
    }
  }
  tbody tr{
    &:nth-child(2){
      background: #bed3ea;
    }
    &:nth-child(3){
      background: #A7C3E0;
    }
    &:nth-child(4){
      background: #8FB2D6;
      color: #fff;
    }
    &:nth-child(5){
      background: #77A2CC;
      color: #fff;
    }
    &:nth-child(6){
      background: #4883B9;
      color: #fff;
    }
  }
  td{
    border-color: #C7DBF0 !important;
  }
}


@media screen and (max-width: 992px){
  .variants{
    padding: .625em 0 1.25em;
    .container{
      padding: 0;
    }
    &__inner{
      padding: 1.25em 1em 0;
    }
    &__items{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
      &.grid3{
        grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
      }
    }
    &__item{
      height: 3.5em;
      span{
        font-size: 1em;
      }
    }
  }

  .product-brand{
    padding: 1.25em 1.5em;
  }

  .complectation{
    display: flex;
    flex-direction: column;
    .text{
      order: 2;
      margin-top: .3125em;
    }
    &__img{
      margin: .625em 0 .3125em;
    }
    .m-desc__nav{
      order: 3;
    }
    &:not(._open){
      .text{
        li:nth-child(2){
          margin-bottom: 0;
          &:after{
            content: '...';
            margin-left: .25em;
          }
          &~li{
            display: none;
          }
        }
      }
    }
  }

  .additional-option{
    &:not(._open){
      .text{
        p:nth-child(3){
          margin-bottom: 0;
          &:after{
            content: '...';
            margin-left: .25em;
          }
          &~p{
            display: none;
          }
        }
      }
    }
  }

  .drawing{
    padding: 0;
    background-color: transparent;
    gap: 1.25em;
    &__bottom,
    &__top{
      border-radius: inherit;
      background-color: #fff;
      padding: 1.25em 1em;
    }
  }

  .drawing-slider{
    margin-top: .75em;
    .swiper{
      &-slide{
        width: 50%;
      }
      &-scrollbar{
        display: block;
      }
    }
  }

  .drawing-card{
    &__title{
      font-size: 1.125em;
    }
  }

  .drawing-table{
    table{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .variants{
    &__inner{
      border-radius: .625em;
    }
    &__items{
      &.grid3{
        .variants__item span{
          font-size: .625em;
        }
      }
    }
    &__item{
      height: 2.5em;
      border-radius: .625em .625em 0 0;
      span{
        font-size: .75em;
      }
    }
  }

  .product-brand{
    border-radius: .625em;
  }

  .drawing-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }

  .drawing-card{
    &__top{
      &:before{
        width: .75em;
        height: .75em;
        margin-right: .75em;
      }
    }
    &__title{
      font-size: .875em;
    }
    &__img{
      img{
        border-radius: .625em;
      }
    }
  }

  .drawing-table{
    overflow-x: auto;
    width: calc(100% + 2em);
    margin-left: -1em;
    padding: 0 1em 1em;
    table{
      min-width: 60em;
    }
  }
}
