.service-detail{
  padding: 1.5em 0;
}

.info-card{
  background-color: #fff;
  padding: 2.5em 2.5em 6.25em;
  border-radius: 1.25em;
  &__icon{
    height: 5em;
    margin-bottom: 1em;
  }
  &__title{
    max-width: 30em;
    &._full{
      max-width: 100%;
    }
    .text18{
      line-height: 140%;
    }
  }
  .text{
    margin-top: 1.875em;
  }
  &--big{
    @media screen and (min-width: 993px){
      grid-row: 1/3;
      grid-column: 2/3;
    }
  }
}

.text{
  &._col2{
    @media screen and (min-width: 993px){
      column-count: 2;
      column-gap: 1em;
      p{
        display: inline-block;
        width: 100%;
      }
    }
  }
  p,li{
    font-size: 1em;
    line-height: 140%;
    @include lastM(0 0 1.25em);
  }
  hr{
    opacity: 0;
    margin: 1.25em 0;
    display: block;
  }
  ._mb0 li,
  ._mb0{
    margin-bottom: 0 !important;
  }
  ol,ul{
    @include lastM(0 0 1.25em);
    &._gap0{
      li{
        margin-bottom: 0;
      }
    }
  }
  ol{
    counter-reset: section;
    &._gap40{
      @include grid100gap(1.25em);
    }
    &._bold-num{
      li:before{
        font-weight: 700;
      }
    }
    li{
      padding-left: 1.5em;
      position: relative;
      counter-increment: section;
      &:before {
        content: counter(section) '. ';
        position: absolute;
        width: auto;
        height: auto;
        left: 0;
        top: 0;
        background-color: transparent;
        font-variant-numeric: tabular-nums;
        -webkit-font-feature-settings: "tnum";
        font-feature-settings: "tnum";
      }
    }
  }
  ul {
    padding-left: .5em;
    li{
      position: relative;
      padding-left: .9375em;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    &._type2{
      column-count: 2;
      column-gap: 3.75em;
      li{
        padding-left: 1.875em;
        &:before{
          content: '';
          width: .375em;
          height: .375em;
          border-radius: 50%;
          background: linear-gradient(180deg, #2f80ed 0%, #00519b 100%);
          top: .5625em;
          left: .4375em;
          box-shadow: 0 0 .25em .125em rgba(47, 128, 237, 0.5);
        }
      }
    }
    &._col2{
      @media screen and (min-width: 993px){
        column-count: 2;
        column-gap: 1em;
      }
    }
    &._col4{
      @media screen and (min-width: 993px){
        column-count: 4;
        column-gap: 2.5em;
      }
    }
  }
}

.consult{
  padding: 2.5em;
  border-radius: 1.25em;
  background-color: $primary;
  color: #fff;
  position: relative;
  overflow: hidden;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: inherit;
    display: block;
    filter: blur(4px);
  }
  &__img2{
    position: absolute;
    //z-index: 2;
    right: 0;
    bottom: 0;
    width: 19.58em;
  }
  &__content{
    position: relative;
    z-index: 2;
  }
  &__title{
    margin-top: 1em;
  }
  &__hint{
    font-weight: 700;
    max-width: 20em;
    margin-top: 2.25em;
    a{
      color: $orange;
    }
  }
  &__nav{
    display: flex;
    margin-top: 5em;
  }
  &__btn{
    padding: 0 4.625em;
  }
}

.service-detail2{
  padding: 1.5em 0 3.125em;
  .info-card{
    @media screen and (min-width: 993px){
      position: relative;
      padding-left: 8.75em;
      &__icon{
        position: absolute;
        left: 2.5em;
        top: 2.5em;
      }
      &__title{
        min-height: 5em;
        display: flex;
        align-items: center;
        max-width: 38em;
      }
    }
    .text{
      max-width: 55em;
      &._full{
        max-width: 100%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .service-detail{
    padding: .3125em 0 .625em;
  }

  .consult{
    padding: 1.25em 1em;
    &__img{
      width: 17.5em;
    }
  }

  .service-detail2{
    padding: .625em 0 .9375em;
  }

  .text{
    ol{
      &._gap40{
        gap: .25em;
      }
    }
    ul{
      &._type2{
        column-count: 1;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .info-card{
    padding: 1.25em 1em;
    border-radius: .625em;
    &__icon{
      height: 3.0625em;
      margin-bottom: 1.25em;
    }
    .text{
      margin-top: .625em;
    }
  }

  .text{
    li,
    p{
      font-size: .75em;
    }
  }

  .consult{
    border-radius: .625em;
    &__img2{
      width: 11em;
    }
    &__title{
      margin-top: .625em;
    }
    &__hint{
      max-width: 9em;
      margin-top: 1em;
      a{
        display: block;
        line-height: inherit;
      }
    }
    &__nav{
      margin-top: 4.125em;
    }
    &__btn{
      width: 100%;
    }
    &__img2{
      right: -1.75em;
    }
  }
}
