.pressure-table,
.product-table{
  ._rect{
    border-radius: 0 !important;
  }
  ._border-top{
    border-top: 2px solid #3374af;
  }
  ._border-right{
    border-right: 2px solid #3374af !important;
  }
}

.pressure-table{
  margin-top: 1.875em;
  th{
    padding: 0 .125em;
    height: 4em;
  }
  thead{
    tr:nth-child(2){
      th{
        height: 5em;
      }
    }
  }
  &__title{
    .text12{
      writing-mode: tb-rl;
      transform: rotate(-180deg);
    }
  }
  th._col1{
    width: 3.75em;
  }
  th._col2{
    width: 15em;
  }
  th._col3,
  th._col4,
  th._col11,
  th._col12,
  th._col13,
  th._col14{
    width: 4.75em;
  }
  th._col5,
  th._col6,
  th._col7{
    width: 7.25em;
  }
  th._col8{
    width: 5em;
  }
  th._col9{
    width: 6em;
  }
  th._col10{
    width: 6.25em;
  }
  th,td{
    text-align: center;
  }
  td{
    border-color: #C7DBF0 !important;
  }
  ._type2{
    background-color: #BED3EA;
  }
  ._type3{
    background-color: #A7C3E0;
  }
}

.marking{
  &__img{
    margin-top: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.product-props{
  padding-bottom: 2.5em;
  margin-top: 1.875em;
  &__text{
    margin-top: 4.25em;
    font-weight: 500;
    font-size: 1em;
    line-height: 193%;
    text-align: center;
  }
}

.product-table{
  thead{
    tr:nth-child(2){
      th{
        height: 4.25em;
      }
    }
  }
  th{
    padding: 0 .5em;
    height: 3.5em;
  }
  th._col1{
    width: 16.25em;
  }
  th._col2,
  th._col3,
  th._col6,
  th._col7,
  th._col8,
  th._col9{
    width: 7.375em;
  }
  th._col4,
  th._col5{
    width: 11.125em;
  }
  td{
    border-color: #dfe9fa !important;
  }
  tbody{
    tr{
      background: $back;
      &:nth-child(odd){
        background: transparent;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .pressure-table{
    margin-top: 1.25em;
    overflow-x: auto;
    width: calc(100% + 2em);
    margin-left: -1em;
    padding: 0 1em 1em;
    table{
      min-width: 85em;
    }
    table{
      font-size: .75em;
    }
  }

  .marking{
    &__img{
      margin-top: .625em;
    }
    &:not(._open){
      .marking__img{
        position: relative;
        overflow: hidden;
        height: 10em;
        @media screen and (min-width: 577px){
          height: 22em;
        }
        &:before{
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: linear-gradient(180deg, rgba(#fff,0) 0%, rgba(#fff,1) 100%);;
        }
      }
    }
  }

  .complectation2{
    &:not(._open){
      p:nth-child(2){
        @include truncate(2);
        &~p{
          display: none;
        }
      }
    }
  }

  .options{
    &:not(._open){
      p:nth-child(4){
        @include truncate(1);
        &:after{
          content: '...';
        }
        &~p{
          display: none;
        }
      }
    }
  }

  .product-desc2{
    &:not(._open){
      p:nth-child(1){
        @include truncate(7);
        margin-bottom: 0;
        &:after{
          content: '...';
        }
        &~p{
          display: none;
        }
      }
    }
  }

  .product-props{
    margin-top: .625em;
    width: calc(100% + 2em);
    margin-left: -1em;
    padding: 1.25em 1em;
    border-radius: 0;
    &__text{
      margin-top: 2em;
      font-size: .875em;
      line-height: 120%;
    }
  }

  .product-table{
    overflow-x: auto;
    width: calc(100% + 2em);
    margin-left: -1em;
    padding: 0 1em 1em;
    table{
      min-width: 75em;
    }
    table{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 576px){
  .product-props{
    &__text{
      font-size: .5625em;
    }
  }
}
