.master-advantages{
  padding: 1.5em 0;
  &__grid{
    padding: 2.5em;
    background-color: #fff;
    border-radius: 1.25em;
  }
  &__info{
    max-width: 16.25em;
    padding-top: 1.5em;
    color: $primary;
  }
}

.advantage-item2{
  padding: 2.5em 1.25em;
  display: flex;
  align-items: center;
  background-color: $back;
  border-radius: 1.25em;
  &__icon{
    width: 3.75em;
    height: 3.5em;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-weight: 500;
    font-size: .875em;
    line-height: 120%
  }
}

.client-advice{
  padding: 1.5em 0 3.125em;
  &__inner{
    background-color: #fff;
    border-radius: 1.25em;
    padding: 2.5em 2.5em 6.25em;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__icon{
    width: 5em;
  }
  &__text{
    margin: 1.875em auto 0;
    width: 48.75em;
    max-width: 100%;
    font-weight: 700;
    .text18{
      line-height: 167%;
    }
    span{
      text-transform: uppercase;
      color: $orange;
    }
    br{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .master-advantages{
    padding: .9375em 0;
    &__grid{
      border-radius: .625em;
      padding: 1.25em 1em;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
    &__info{
      padding-top: 0;
    }
  }

  .advantage-item2{
    padding: 1.25em 1.5em;
    border-radius: .625em;
    align-items: flex-start;
    &__icon{
      flex-shrink: 0;
      margin-right: 1em;
      width: 2.125em;
      height: 2em;
    }
    &__title{
      font-size: .75em;
    }
  }

  .client-advice{
    padding: .9375em 0 .9375em;
    &__inner{
      padding: 1.25em 1em;
      border-radius: .625em;
    }
    &__icon{
      width: 3.625em;
    }
    &__text{
      margin-top: 1.25em;
      .text18{
        line-height: 140%;
      }
      span{
        font-size: 1.33em;
      }
      br{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .master-advantages{
    &__grid{
      grid-template-columns: 100%;
    }
    &__info{
      margin-bottom: .625em;
    }
  }
}
