.catalog-item{
  padding: 1.5em 0;
  &__grid{
    display: grid;
    grid-template-columns: 13.75em calc(100% - 16.25em);
    gap: 1.625em 2.5em;
  }
  @media screen and (min-width: 993px){
    &__grid{
      grid-template-rows: 5em auto;
    }
    &__col{
      grid-column: 1/2;
      grid-row: 1/3;
    }
  }
  &__back{
    margin-top: 1.25em;
    justify-content: space-between;
    padding: 0 2.5em;
    svg{
      width: .5625em;
    }
    &:hover{
      path{
        stroke: #fff;
      }
    }
  }
}

.catalog-card{
  padding: 2em 1.25em;
  background: linear-gradient(135deg, #00519b 0%, #4184f3 100%);
  border-radius: 1.25em;
  color: #fff;
  min-height: 13.75em;
  position: relative;
  overflow: hidden;
  &__top{
    max-width: 9em;
  }
  &__title{
    font-weight: 700;
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 8.3125em;
    max-height: 100%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.catalog-slider{
  display: grid;
  grid-template-columns: calc(100% - 8.75em) 5em;
  gap: 3.75em;
  .swiper{
    &-slide{
      width: 25%;
    }
    &-button{
      position: static;
      transform: unset;
      margin: 0;
    }
  }
}

.catalog-card2{
  padding: 1.25em 3.75em 1.25em 1.25em;
  height: 100%;
  background: #c5d8fa;
  border-radius: 1.25em;
  min-height: 5em;
  display: flex;
  align-items: center;
  overflow: hidden;
  position: relative;
  &__title{
    font-size: .75em;
    line-height: 125%;
    font-weight: 700;
  }
  &__img{
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 3.75em;
    max-height: 3.125em;
  }
}

.catalog-nav{
  padding: 1.75em 2.5em;
  background-color: #fff;
  border-radius: 1.25em;
  &__links{
    column-count: 2;
    column-gap: 3em;
    max-width: 50.5em;
    min-height: 100%;
    &._small{
      max-width: 40em;
    }
    &._small2{
      max-width: 20em;
    }
    &._big{
      max-width: 100%;
      .catalog-nav__link{
        display: inline-block;
      }
    }
    &._col3{
      column-count: 3;
      max-width: 30em;
    }
  }
  &__link{
    font-weight: 600;
    font-size: .875em;
    line-height: 120%;
    width: 100%;
    margin-bottom: 1.25em;
    display: block;
    &:last-child{
      margin-bottom: 0;
    }
    &._active{
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding: .625em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: .625em;
    }
    &__back{
      display: none;
    }
  }

  .catalog-card{
    min-height: 5.75em;
    padding: 1em;
    border-radius: .625em;
    display: flex;
    align-items: center;
    &__title{
      font-size: .75em;
    }
    &__img{
      padding: .625em 0 0;
    }
  }

  .catalog-slider{
    order: -1;
    margin-bottom: .625em;
    grid-template-columns: 100%;
    .swiper{
      &-button{
        display: none;
      }
      &-container{
        width: calc(100% + .5em);
      }
      &-slide{
        padding-right: .5em;
        width: calc(100% / 3);
      }
      &-scrollbar{
        display: block;
      }
    }
  }

  .catalog-card2{
    padding: .625em 1em;
    border-radius: .625em;
    min-height: 5.75em;
    align-items: flex-start;
    &__title{
      font-size: .5625em;
    }
    &__img{
      max-width: 4.625em;
      max-height: 2.8125em;
    }
  }

  .catalog-nav{
    padding: 1.25em 1em;
    border-radius: .625em;
    &__links{
      column-count: 1;
    }
    &__link{
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .catalog-nav{
    &__link{
      font-size: .75em;
    }
  }
}
