.work-card{
  height: 100%;
  padding: 2.5em;
  background-color: #fff;
  border-radius: 1.25em;
  display: flex;
  flex-direction: column;
  &__img{
    @include imgCover(86.1%);
    border-radius: 1.25em;
  }
  &__info{
    margin-top: 1.25em;
    @include grid100gap(1.25em);
    font-weight: 600;
    text-align: center;
  }
  &__desc{
    min-height: 2.42em;
  }
  &__items{
    margin-top: .75em;
    @include grid100gap(.625em);
  }
  &__item{
    display: flex;
    align-items: center;
    font-weight: 500;
    &-icon{
      width: 3.75em;
      margin-right: .625em;
    }
    &-title,
    &-desc{
      font-size: 1em;
      line-height: 125%;
    }
    &-title{
      color: $primary;
      font-weight: 700;
    }
    &-desc{
      margin-left: auto;
      padding-left: .5em;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.25em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    padding: 0 4.1875em;
  }
}

.work-modal{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 25em) 22.5em;
    gap: 2.5em;
  }
  @media screen and (min-width: 993px){
    .gallery{
      .swiper-slide:not(:first-child){
        .gallery-item{
          border-radius: .625em;
        }
      }
    }
  }
  .work-card__info{
    text-align: left;
    margin: 0;
  }
  .work-card__items{
    margin-top: 1.75em;
    padding: 1.5em 2.5em 1.5em 1.25em;
    background-color: $back;
    border-radius: 1.25em;
  }
  .work-card__item{
    &-icon{
      width: 2.5em;
      height: 2.5em;
      border-radius: .625em;
    }
    &-title{
      font-size: .875em;
    }
  }
}

.work-feedback{
  padding: 2.5em 3.75em 1.25em;
  background-color: #d2e1fb;
  background-image: url(../img/work-feedback-bg.svg);
  background-repeat: no-repeat;
  background-position: right top;
  background-size: auto 100%;
  border-radius: 1.25em;
  margin-top: 1.25em;
  color: $primary;
  text-align: center;
  font-weight: 700;
  &__hint{
    a{
      white-space: nowrap;
      color: $orange;
    }
    .text24,
    .text14{
      line-height: 140%;
    }
  }
  &__btn{
    margin: 1.25em auto 0;
    width: 13.75em;
    max-width: 100%;
    padding: 0 1em;
  }
}


@media screen and (max-width: 992px){
  .works{
    padding: .625em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }

  .work-card{
    padding: 1.25em 1em;
    border-radius: .625em;
    &__img{
      border-radius: .625em;
    }
    &__title,
    &__desc{
      font-size: .75em;
    }
    &__items{
      margin-top: 1.25em;
    }
    &__item{
      &-icon{
        width: 3.5625em;
      }
      &-title,
      &-desc{
        font-size: .875em;
      }
    }
    &__nav{
      padding-top: 1.5em;
    }
    &__btn{
      width: 100%;
      padding: 0;
    }
  }

  .work-modal{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    .gallery{
      width: 15em;
    }
    .gallery-item{
      border-radius: .625em;
    }
    .work-card__info{
      gap: .625em;
    }
    .work-card__items{
      margin-top: 1.25em;
      padding: 1.25em 1em;
      border-radius: .625em;
    }
    .work-card__item{
      &-icon{
        width: 2em;
        height: 2em;
      }
      &-desc{
        font-size: .875em;
      }
    }
  }

  .work-feedback{
    padding: 1.25em 1em;
    background-image: url(../img/work-feedback-bg.svg);
    &__hint{
      a{
        font-size: .875em;
      }
    }
    &__btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .works{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
