@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.5em;
    height: 5em;
    border-radius: 1.25em;
    font-weight: 600;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .875em;
    }
}

.m-btn-orange{
    background-color: $orange;
    color: #fff;
    &:hover{
        background-color: darken($orange,10%);
        color: #fff;
    }
    &:disabled{
        background: #a1a1a1;
        color: #fff;
        pointer-events: none;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.m-btn-blue-outline{
    border: 2px solid $primary;
    color: $primary;
    @media screen and (min-width: 993px){
        &:hover{
            background-color: $primary;
            color: #fff;
        }
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.75em;
        border-radius: .625em;
    }
}


