.catalog-item2{
  padding: 1.5em 0;
  @media screen and (min-width: 993px){
    &__grid:not(._type2){
      grid-template-rows: 4.875em auto;
      .gallery{
        grid-row: 1/3;
        grid-column: 1/2;
      }
    }
    &__info{
      grid-column: 2/4;
      grid-row: 1/2;
      padding-top: 1.25em;
      .text24{
        @include truncate(2);
      }
      &._type2{
        margin-bottom: 3em;
      }
    }
  }
}

.gallery{
  &__small{
    margin-top: 1.25em;
    .swiper {
      &-slide{
        width: 25%;
        cursor: pointer;
      }
    }
  }
  @media screen and (min-width: 993px){
    .swiper {
      width: calc(100% + .8125em);
      &-slide{
        padding-right: .8125em;
      }
    }
  }
}

.gallery-item{
  @include imgCover(100%);
  border-radius: 1.25em;
}

.m-desc{
  &__nav{
    display: none;
  }
}

.props{
  padding: 2.5em 2.5em 1em;
  background: #c5d8fa;
  border-radius: 1.25em;
  .text{
    margin-top: 1em;
    p{
      @include lastM(0 0 .75em)
    }
  }
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1.5em);
    &._type2{
      grid-template-columns: repeat(2,calc(50% - 1.25em));
      gap: 2.5em;
      align-items: flex-start;
      .props__item{
        display: inline-block;
        width: 100%;
      }
      .props__item-title,
      .props__item-desc{
        font-size: .875em;
      }
    }
  }
  &__group{
    @include grid100gap(1.5em);
  }
  &__item{
    &-title,
    &-desc{
      font-size: 1em;
      line-height: 130%;
    }
    &-title{
      font-weight: 700;
    }
  }
}

.catalog-feedback{
  border-radius: 1.25em;
  background-color: $primary;
  background-image: url(../img/catalog-feedback-bg.svg);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 23.75em auto;
  padding: 2.5em;
  color: #fff;
  display: flex;
  flex-direction: column;
  &__title{
    margin-top: .8125em;
    font-weight: 700;
  }
  &__hint{
    margin-top: 1.75em;
    font-weight: 700;
    max-width: 20em;
    a{
      white-space: nowrap;
      color: $orange;
    }
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.25em;
  }
  &__btn{
    width: 100%;
    @media screen and (min-width: 993px){
      text-transform: uppercase;
      span{
        font-size: 1.125em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item2{
    padding: .625em 0;
    overflow: hidden;
    &__grid{
      grid-template-columns: 100%;
    }
    &__info{
      order: -1;
      &._type2{
        margin-bottom: 1.25em;
      }
    }
    .gallery{
      width: 17em;
      margin: 0 auto;
      &._type2{
        width: 100%;
      }
    }
  }

  .gallery{
    .swiper{
      overflow: unset;
      &-container{
        width: calc(100% + .5em);
      }
      &-slide{
        padding-right: .5em;
      }
    }
    &__small{
      display: none;
    }
  }

  .props{
    order: 2;
    padding: 1.25em 1em;
    &__items{
      margin-top: 1em;
      gap: 1em;
    }
    &__group{
      gap: 1em;
    }
    &--type1{
      &:not(._open){
        .props__item:nth-child(2){
          .props__item-desc{
            @include truncate(1);
          }
          &~.props__item{
            display: none;
          }
        }
      }
    }
    &--type2{
      &:not(._open){
        .text{
          @include truncate(5);
          *{
            display: inline;
          }
        }
      }
    }
  }

  .m-desc{
    &._open{
      .m-desc__btn{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
    &__nav{
      margin-top: 1em;
      display: block;
    }
    &__btn{
      width: 100%;
      font-weight: 500;
      ._view2{
        display: none;
      }
    }
  }

  .catalog-feedback{
    padding: 1.25em 1em;
    &__title{
      display: none;
    }
    &__hint{
      margin-top: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item2{
    .gallery{
      width: 15em;
    }
  }

  .m-desc{
    &__btn{
      height: 2.875em;
      border-radius: .3125em;
      border-width: 1px;
      span{
        font-size: .75em;
      }
    }
  }

  .props{
    border-radius: .625em;
    &__items{
      &._type2{
        grid-template-columns: 1fr .8fr;
        gap: 1.5em;
        .props__item-title,
        .props__item-desc{
          font-size: .75em;
        }
      }
    }
    &__item{
      &-title,
      &-desc{
        font-size: .875em;
      }
    }
  }

  .catalog-feedback{
    background-image: url(../img/catalog-feedback-bg2.svg);
    background-size: 13.5625em auto;
    background-position: right top;
    border-radius: .625em;
  }
}
