.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .875em;
    line-height: 121%;
    font-weight: 700;
    color: $primary;
    margin-bottom: .71em;
  }
  input,
  textarea{
    font-family: $font1;
    border-radius: 1.42em;
    border: 1px solid $back;
    padding: 0 1.42em;
    height: 4.3em;
    width: 100%;
    font-size: .875em;
    background-color: $back;
    color: $black;
    &::placeholder {
      color: #7C9BD3;
    }
    &:focus {
      border-color: #b6cbf0;
    }
  }
  textarea{
    padding-top: 1.42em;
    padding-bottom: 1.42em;
    height: 15.21em;
    resize: none;
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 5em;
    min-height: 3.75em;
    font-weight: 500;
    span{
      font-size: .875em;
      line-height: 140%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 1px solid $back;
      width: 3.75em;
      height: 3.75em;
      @include bgImgSize(0);
      background-color: $back;
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
      color: $primary;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        border-color: #b6cbf0;
        background-size: 1.5em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: 1.25em;
      background-image: url(../img/check.svg);
    }
  }
  &._gray{
    label{
      color: #787878;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    input,
    textarea{
      padding-left: 1.2em;
      padding-right: 1.2em;
      border-radius: .71em;
    }
    textarea{
      height: 9.3em;
      padding: 1.2em;
    }
  }

  .m-check{
    input:checked~label{
      &:before{
        background-size: 1em auto;
      }
    }
    label{
      min-height: 2.5em;
      padding-left: 2.875em;
      &:before{
        width: 2.5em;
        height: 2.5em;
        border-radius: .625em;
      }
    }
  }
}


