.consult2,
.consult3{
  padding: 1.5em 0;
  &__inner {
    padding: 2.5em;
    border-radius: 1.25em;
    background-color: $primary;
    color: #fff;
  }
}

.consult2{
  &__inner{
    padding-right: 3.75em;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    background-image: url(../img/consult-bg.svg);
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: right top;
  }
  &__content{
    max-width: 45em;
  }
  &__title{
    margin-top: .625em;
  }
  &__hint{
    font-weight: 700;
    margin-top: .5em;
    a{
      color: $orange;
    }
    @media screen and (min-width: 993px){
      br{
        display: none;
      }
    }
  }
  &__btn{
    padding: 0 4.625em;
  }
}

.mounting-detail{
  padding: 1.5em 0;
  .advantage-item{
    min-height: 16.25em;
  }
}

.mounting-contain{
  grid-column: 1/2;
  grid-row: 1/3;
  border: 3px solid #d2e1fb;
  border-radius: 1.25em;
  padding: 6em 2.5em;
  color: $primary;
  position: relative;
  &__img{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 17.875em;
    max-width: 100%;
    &._view2{
      display: none;
    }
  }
}

.consult3{
  &__inner{
    background-image: url(../img/consult-bg3.svg);
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 61.125em auto;
    display: grid;
    grid-template-columns: 25.5em 24.5em calc(100% - 55em);
    gap: 2.5em;
    align-items: flex-end;
  }
  &__title{
    margin-top: .625em;
  }
  &__hint{
    border: 2px solid $lightBlue2;
    padding: 1em 1.25em;
    border-radius: 1.25em;
    display: flex;
    align-items: center;
    color: $lightBlue2;
    img{
      width: 2.125em;
      margin-right: 1.25em;
    }
    a{
      font-weight: 600;
    }
  }
}


@media screen and (max-width: 992px){
  .consult2{
    padding: .9375em 0;
    &__inner{
      padding: 1.25em 1em;
      background-image: url(../img/consult-bg2.svg);
    }
    &__content{
      max-width: 22em;
    }
    &__title{
      display: none;
    }
    &__hint{
      margin-top: .625em;
    }
  }

  .mounting-detail{
    padding: .9375em 0;
    &__grid{
      grid-template-columns: repeat(4,calc(25% - 1.5em / 4));
      gap: .5em;
    }
    .advantage-item{
      min-height: 8.75em;
    }
  }

  .mounting-contain{
    padding: 1.875em 1em;
    border-radius: .625em;
    &__desc{
      font-size: .875em;
    }
    &__img{
      width: 80%;
    }
  }

  .consult3{
    &__inner{
      grid-template-columns: 100%;
      gap: .625em;
      padding: 1.25em 1em;
      background-size: auto 100%;
    }
    &__title{
      max-width: 18.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .consult2{
    &__inner{
      border-radius: .625em;
      display: block;
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
      padding: 0;
    }
  }

  .mounting-detail{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .25em));
      row-gap: .625em;
    }
  }

  .mounting-contain{
    grid-column: 1/-1;
    grid-row: unset;
    &__img{
      right: 1em;
      left: unset;
      width: 7.5625em;
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
  }

  .consult3{
    &__inner{
      border-radius: .625em;
      background-image: url(../img/consult-bg4.svg);
    }
    &__title{
      max-width: 11.5em;
    }
    &__hint{
      align-items: flex-start;
      padding: .5em;
      border-radius: .625em;
      img{
        margin-right: .5em;
      }
      .text16{
        max-width: 13.875em;
      }
      a{
        white-space: nowrap;
      }
    }
  }
}
